//vue2
import { mapMutations, mapState } from 'vuex';
import MusicDetail from '@/components/item/MusicDetail.vue';
export default {
  name: 'FooterMusic',
  components: {
    MusicDetail
  },
  data() {
    return {
      isPlay: false,
      interVal: 0
    };
  },
  computed: {
    ...mapState(['PlayList', 'PlayListIndex', 'isbtnShow', 'detailShow', 'currentTime'])
  },
  mounted() {
    // console.log(this.$refs.audio)
    this.$store.dispatch('getLyric', this.$store.state.PlayList[this.$store.state.PlayListIndex].id);
    // this.updateTime()
  },

  updated() {
    this.$store.dispatch('getLyric', this.$store.state.PlayList[this.$store.state.PlayListIndex].id);
    this.addDuration();
  },
  methods: {
    ...mapMutations(['changeIsbtnShow', 'updateDetailShow', 'updateCurrentTime', 'updateDuraction']),
    play() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.changeIsbtnShow(true);
      } else {
        this.$refs.audio.pause();
        this.changeIsbtnShow(false);
      }
    },
    updateTime() {
      // console.log(this.$refs.audio.currentTime)
      this.updateCurrentTime(this.$refs.audio.currentTime);
    },
    addDuration() {
      this.updateDuraction(this.$refs.audio.duration);
    },
    changeAudio(val) {
      // console.log([this.$refs.audio.currentTime])
      // this.$refs.audio.currentTime = val
    }
  },
  watch: {
    PlayListIndex() {
      //监听下标，如果下标发生了改变，就自动播放音乐
      this.$refs.audio.autoplay = true;
      if (this.$refs.audio.paused) {
        //本来是暂停状态，点击音乐之后开始播放并改变图标
        this.changeIsbtnShow(true);
      }
    },
    PlayList() {
      if (!this.isbtnShow) {
        this.$refs.audio.autoplay = true;
        this.changeIsbtnShow(true);
      }
    }
  }
};